import flatpickr from 'flatpickr';
import 'flatpickr/dist/flatpickr.min.css';
import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';

let userSelectedDate;

document.querySelector('[data-start=""]').disabled = true;

const options = {
  enableTime: true,
  time_24hr: true,
  defaultDate: new Date(),
  minuteIncrement: 1,
  onClose(selectedDates) {
    userSelectedDate = selectedDates[0];
    console.log(selectedDates[0]);

    validateDate(selectedDates[0]);
  },
};

const fp = flatpickr('#datetime-picker', options);

function validateDate(date) {
  const currentDate = new Date();
  const selectedDate = new Date(date);

  if (selectedDate < currentDate) {
    iziToast.error({
      title: 'Error',
      position: 'topRight',
      message: 'Please choose a date in the future',
    });
    document.querySelector('[data-start=""]').disabled = true;
  } else {
    document.querySelector('[data-start=""]').disabled = false;
  }
}

function addLeadingZero(value) {
  return String(value).padStart(2, '0');
}

function convertMs(ms) {
  const second = 1000;
  const minute = second * 60;
  const hour = minute * 60;
  const day = hour * 24;

  const days = Math.floor(ms / day);
  const hours = Math.floor((ms % day) / hour);
  const minutes = Math.floor(((ms % day) % hour) / minute);
  const seconds = Math.floor((((ms % day) % hour) % minute) / second);

  return {
    days: addLeadingZero(days),
    hours: addLeadingZero(hours),
    minutes: addLeadingZero(minutes),
    seconds: addLeadingZero(seconds),
  };
}

const timer = {
  deadline: userSelectedDate,
  elements: {
    days: document.querySelector('[data-days=""]'),
    hours: document.querySelector('[data-hours=""]'),
    minutes: document.querySelector('[data-minutes=""]'),
    seconds: document.querySelector('[data-seconds=""]'),
  },
  intervalId: null,

  start() {
    this.intervalId = setInterval(() => {
      const timeLeft = this.deadline - Date.now();

      if (timeLeft <= 0) {
        clearInterval(this.intervalId);
        this.stop();
        return;
      }

      const { days, hours, minutes, seconds } = convertMs(timeLeft);
      this.elements.days.textContent = days;
      this.elements.hours.textContent = hours;
      this.elements.minutes.textContent = minutes;
      this.elements.seconds.textContent = seconds;
    }, 1000);

    document.querySelector('[data-start=""]').disabled = true;
    document.querySelector('#datetime-picker').disabled = true;
  },

  stop() {
    clearInterval(this.intervalId);
    document.querySelector('#datetime-picker').disabled = false;
  },
};

document.querySelector('[data-start=""]').addEventListener('click', () => {
  if (userSelectedDate) {
    timer.deadline = new Date(userSelectedDate);
    timer.start();
  }
});

document.querySelector('#datetime-picker').addEventListener('click', () => {
  fp.open();
});
